import { Component } from '@angular/core'
import { CommonModule } from '@angular/common'
import { FormsModule } from '@angular/forms'
import {
  IonContent,
  IonRefresher,
  IonProgressBar,
  IonSkeletonText,
  IonList,
  IonItem,
  IonInput,
  IonButton,
  IonSelect,
  IonSelectOption,
  IonText,
  IonCard,
  IonCardHeader,
  IonCardSubtitle,
  IonCardContent,
  IonThumbnail,
  IonIcon,
  IonNote,
  IonLabel,
  IonBadge,
  IonRow,
  IonCol,
  LoadingController,
} from '@ionic/angular/standalone'
import { NfcService, NfcState } from 'src/app/services/api-wrappers/nfc.service'
import { HeaderComponent } from 'src/app/components/header/header.component'
import { StorageService } from 'src/app/services/utils/storage.service'
import { addIcons } from 'ionicons'
import { idCardOutline, trashOutline } from 'ionicons/icons'
import { TranslateModule, TranslateService } from '@ngx-translate/core'
import { ErrorHandlerService } from 'src/app/services/utils/error-handler.service'

export type PassModel = {
  model: string
  picture?: string
  prefix?: string
  suffix?: string
  regex?: RegExp
}

export type NewPassState =
  | {
      state: 'loading'
    }
  | {
      state: 'models'
      model: PassModel
      models: PassModel[]
      newPass: { pass: string; valid?: boolean }
    }

@Component({
  selector: 'app-nfc',
  templateUrl: './nfc.page.html',
  styleUrls: ['./nfc.page.scss'],
  imports: [
    IonContent,
    CommonModule,
    FormsModule,
    IonRefresher,
    IonProgressBar,
    HeaderComponent,
    IonSkeletonText,
    IonList,
    IonItem,
    IonInput,
    IonButton,
    TranslateModule,
    IonSelect,
    IonSelectOption,
    IonText,
    IonCard,
    IonCardHeader,
    IonCardSubtitle,
    IonCardContent,
    IonThumbnail,
    IonIcon,
    IonNote,
    IonLabel,
    IonBadge,
    IonRow,
    IonCol,
  ],
})
export class NfcPage {
  nfcState: NfcState
  passModels: any[]
  newPassState: NewPassState = { state: 'loading' }

  constructor(
    private nfcService: NfcService,
    private storage: StorageService,
    private loadingCtrl: LoadingController,
    private translateService: TranslateService,
    private errorHandler: ErrorHandlerService
  ) {
    addIcons({ idCardOutline, trashOutline })

    this.storage.get('program').then((program) => {
      if (program && program.config.authorised_passes?.length > 0) {
        this.newPassState = {
          state: 'models',
          model: program.config.authorised_passes![0],
          models: program.config.authorised_passes!,
          newPass: { pass: '' },
        }
      } else {
        const passes: PassModel[] = [
          { model: this.translateService.instant('nfc.card_model') },
        ]
        this.newPassState = {
          state: 'models',
          model: passes[0],
          models: passes,
          newPass: { pass: '' },
        }
      }
    })
    this.nfcService.nfcState.subscribe((state) => {
      this.nfcState = state
    })
  }

  handleRefresh(event: any) {
    this.refresh().then(() => {
      event.target.complete()
    })
  }

  refresh(): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      this.nfcService.getPasses().then(() => {
        resolve()
      })
    })
  }

  onPassChange(event: any) {
    if (this.newPassState.state === 'models') {
      this.newPassState.newPass.valid = event.detail.event.target.validity.valid
    }
  }

  async associatePass() {
    if (this.newPassState.state === 'models') {
      const loading = await this.loadingCtrl.create({
        message: this.translateService.instant('pass.associating'),
        duration: 3000,
      })

      loading.present()
      this.nfcService
        .associatePass(
          this.newPassState.newPass.pass,
          this.newPassState.model.model
        )
        .then(async () => {
          await loading.dismiss()
          this.refresh()
        })
        .catch(async (err) => {
          await loading.dismiss()
          this.errorHandler.handleError(err)
        })
    }
  }

  async dissociatePass(pass: string) {
    const loading = await this.loadingCtrl.create({
      message: this.translateService.instant('pass.dissociating'),
    })

    loading.present()
    this.nfcService
      .disassociatePass(pass)
      .then(async () => {
        await loading.dismiss()
        this.refresh()
      })
      .catch(async (err) => {
        await loading.dismiss()
        this.errorHandler.handleError(err)
      })
  }
}
